<template>
	<view class="home">
		<view :style="{height: statusBarHeight + 'px'}"></view>
		<view class="btn">
			<button type="default" @click="show">点击</button>
		</view>
		<view class="scroll-box">
			<scroll-view scroll-y class="scroll-view" :style="{height: otherHeight + 'px'}">
				<view class="scroll-item" v-for="item in 100">{{item}}</view>
			</scroll-view>
		</view>
	</view>
</template>
<script>
	export default {
		data() {
			return {
				statusBarHeight: 20,
				windowHeight: 0,
				windowWidth: 375,
				otherHeight: 0
			}
		},
		onUnload: function() {
			
		},
		onLoad() {
			
		},
		methods: {
			show() {
				const info = uni.getSystemInfoSync()
				this.statusBarHeight = info.statusBarHeight;
				this.windowHeight = info.windowHeight;
				this.windowWidth = info.windowWidth;
				this.otherHeight = this.windowHeight - this.statusBarHeight - 100;
				console.log(this.statusBarHeight,this.windowHeight,this.windowWidth)
				console.log(info.screenHeight)
				console.log(info.screenWidth)
			}
		}
	}
</script>

<style>
	.btn {
		height: 100px;
		border: 1px solid #09BB07;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.scroll-view {
		border: 3px solid blue;
		box-sizing: border-box;
		height: 200px;
	}

	.scroll-item {
		border: 1px solid red;
		display: inline-block;
		width: 100%;
		height: 100%;
		text-align: center;
		line-height: 200px;
		box-sizing: border-box;
	}
</style>
